.reversi-square {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  border: 2px solid #333;
  box-sizing: border-box;
  cursor: pointer;
  border-radius: 2px;
  margin-right: 2px;
}

.reversi-piece {
  border-radius: 50%;
  width: 32px;
  height: 32px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.reversi-black-piece {
  background-color: #000;
}

.reversi-white-piece {
  background-color: #fff;
  border: 2px solid #000;
}

.reversi-square.reversi-valid-move {
  border: 4px solid green;
}

.reversi-square.reversi-previous-move {
  border: 4px solid rgb(235, 179, 90);
}

.reversi-small {
  transform: scale(0.7);
}

.reversi-tiny {
  transform: scale(0.6);
}
